#uo-ld-group-drip {
	.ultp-gutenberg-field-select {
		height: 26px !important;
		margin-top: 1px !important;
		padding: 2px 20px 2px 10px!important;
	}

	.ultp-gutenberg-btn:not(:last-child) {
		margin-right: 5px;
	}

	.uo-ld-group-drip-row {
		$row_class: '.uo-ld-group-drip-row';

		transition: opacity 150ms ease-in-out;

		&--loading {
			pointer-events: none;
			opacity: .6;
		}

		&__actions {
			position: relative;

			padding-right: 30px;

			&:before {
				@include icons-font;
				content: '';
				font-size: 15px;

				color: #4ab865;

				opacity: 0;

				position: absolute;
				top: 50%;
				right: 10px;
				transform: translateY(-50%);

				transition: opacity 150ms ease-in-out;
			}
		}

		&--success {
			#{ $row_class }__actions {
				&:before {
					content: $icon-check;
					opacity: 1;
				}
			}
		}

		&--error {
			#{ $row_class }__actions {
				&:before {
					content: $icon-times;
					color: #d94e4f;
					opacity: 1;
				}
			}
		}
	}
}