/**
 * Sorry for all the !important on this file guys
 * LearnDash tries to overwrite our styles
 */

.ultp-gutenberg {
	&-field {
		&-text {
			font-size: 13px !important;
			font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen-Sans,Ubuntu,Cantarell,Helvetica Neue,sans-serif !important;

			box-shadow: 0 0 0 transparent !important;

			transition: box-shadow .1s linear !important;
			border-radius: 4px !important;

			border: 1px solid #8d96a0 !important;

			background-color: #fff !important;
			color: #32373c !important;
			outline: 0 !important;

			margin: 1px !important;

            line-height: 1 !important;

			&:focus {
				color: #191e23 !important;
				border-color: #00a0d2 !important;
				box-shadow: 0 0 0 1px #00a0d2 !important;
				outline: 2px solid transparent !important;
				outline-offset: -2px !important;
			}
		}

		&-select {
			font-size: 13px !important;
			font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen-Sans,Ubuntu,Cantarell,Helvetica Neue,sans-serif !important;
			
			padding: 6px 8px !important;

			box-shadow: 0 0 0 transparent !important;

			transition: box-shadow .1s linear !important;
			border-radius: 4px !important;

			border: 1px solid #8d96a0 !important;

			background-color: #fff !important;
			color: #32373c !important;
			outline: 0 !important;

			margin: 1px !important;

			vertical-align: middle !important;

			option {
				color: #32373c !important;
			}

			&:focus {
				color: #191e23 !important;
				border-color: #00a0d2 !important;
				box-shadow: 0 0 0 1px #00a0d2 !important;
				outline: 2px solid transparent !important;
				outline-offset: -2px !important;
			}
		}

		&--invalid {
			border-color: #db3132 !important;

			&:focus {
				border-color: #db3132 !important;
				box-shadow: 0 0 0 1px #db3132 !important;
			}
		}
	}

	&-btn {
		display: inline-flex;
		text-decoration: none;
		font-size: 13px;
		margin: 0;
		border: 0;
		cursor: pointer;
		-webkit-appearance: none;
		background: none;
		padding: 0 10px 1px;
		line-height: 26px;
		height: 28px;
		border-radius: 3px;
		white-space: nowrap;
		border-width: 1px;
		border-style: solid;
		color: #555;
		border-color: #ccc;
		background: #f7f7f7;
		box-shadow: inset 0 -1px 0 #ccc;
		vertical-align: top;

		position: relative;

		outline: none !important;

		&:hover {
			background: #fafafa;
			border-color: #999;
			box-shadow: inset 0 -1px 0 #999;
			color: #23282d;
			text-decoration: none;
		}

		&:active {
			background: #eee;
			border-color: #999;
			box-shadow: inset 0 1px 0 #999;
		}

		&:focus {
			box-shadow: inset 0 -1px 0 #999, 0 0 0 2px #bfe7f3;
			text-decoration: none;
		}

		&--loading {
			color: transparent !important;
			pointer-events: none;

			&:hover,
			&:active,
			&:focus {
				color: transparent !important;
			}

			&:before {
				position: absolute;
				top: 50%;
				left: 50%;

				@include icons-font;
				content: $icon-spinner-third;
				font-size: 16px;

				color: #555;

				@include animation-rotate( 'ultp-group-drip-loading-icon', 1.5s, 0deg, 365deg, -50%, -50% );
			}
		}
	}
}