.ultp-restrict-access {
	$class: &;

	padding: 10px 0;

	&,
	& * {
		box-sizing: border-box;
	}

	input[type="checkbox"],
	input[type="radio"] {
		border: 2px solid #6c7781;
		margin-right: 5px;
	}

	#post-body & {
		input[type=radio]:checked::before {
			margin: 2px;
		}

		input[type=checkbox]:checked::before {
			margin-top: -4px;
			margin-left: -5px;
		}
	}

	&-options {
		display: none;

		#{ $class }--enabled & {
			display: block;
		}

		#{ $class }__option {
			padding-top: 12px;

			&--redirect-to-post-option {
				display: none;

				#{ $class }--redirect-to-post & {
					display: block;
				}
			}

			&--redirect-to-custom-url-option {
				display: none;

				#{ $class }--redirect-to-custom_url & {
					display: block;
				}
			}
		}

		&-redirect {
			display: none;

			#{ $class }--redirect-if-denied & {
				display: block;
			}
		}
		
		label {
			color: #555d66;
			font-size: 13px;
			display: inline-block;
			margin-bottom: 4px;
		}

		select,
		.select2-container {
			width: 100% !important;
		}

		.select2-selection--multiple {
			border-color: #7e8993 !important;
			border-width: 1px !important;

			.select2-search__field {
				&,
				&:hover,
				&:active,
				&:focus {
					border: 0 !important;
					box-shadow: none !important;
					margin-top: 0 !important;
				}
			}
		}

		.select2-selection--single {
			box-shadow: none !important;
			outline: 0 !important;
		}

		input[type="text"] {
			width: 100%;
		}
	}
}